.editor_wrapper {
  width: 100vw;
  height: 100vh;
  background: #711;
  text-align: center;
}

.textfield,
.preview {
  text-align: left;
  padding: 0 2vw 0 2vw;
  background: white;
}

.textfield {
  height: 30vh;
  width: 94%;
  background: #711;
  color: #fee;
  border: none;
  resize: none;
}

.preview {
  height: 68vh;
  overflow: scroll;
  border: 1px solid black;
}

code {
  background: #fee;
}

pre code {
  display: block;
}

blockquote {
  background: #ffe;
  font-family: serif;
  font-style: italic;
  line-height: 2em;
  position: relative;
  border-left: 2px solid #110;
  padding-left: 8px;
  margin-block-end: 0;
}

.preview td, .preview th {
  border: 1px solid #111;
}
